import {iScroll} from '../iscroll/iscroll'

let scroll, upFn, downFn, pullDownEl, pullDownOffset, pullUpEl, pullUpOffset, downText, upText, pages = 1, page = 1,
  onlyRefs = false,
  name,//favorite、service-list、publish-list、home、server
  lock = false;

function up() {
  setTimeout(function () {
    upFn();
  }, 500);
}

function down() {
  setTimeout(function () {
    downFn();
  }, 500);
}
/**
 * 初始化iScroll控件
 */
function loaded(config) {
  if (config.onlyRefs) {
    onlyRefs = config.onlyRefs;
  } else {
    onlyRefs = false;
  }
  pullDownEl = document.getElementById(config.down);
  pullDownOffset = pullDownEl.offsetHeight;
  downText = config.downText;
  downFn = config.downFn;
  if (!onlyRefs) {
    pullUpEl = document.getElementById(config.up);
    pullUpOffset = pullUpEl.offsetHeight;
    upText = config.upText;
    upFn = config.upFn;
  }

  pages = config.pages;
  page = 1;
  lock = false;//首次加载 关闭lock

  if (page >= pages) {
    pullUpEl.querySelector(upText).innerHTML = '数据已加载完...';
  }

  name = config.name;
  scroll = new iScroll(config.wrapper, {
    scrollbarClass: 'myScrollbar', /* 重要样式 */
    loadingY: 0,
    hScroll: false,
    onRefresh: function () {
      if (pullDownEl.className.match('loading')) {
        pullDownEl.className = '';
        pullDownEl.querySelector(downText).innerHTML = '下拉刷新...';
      } else if (!onlyRefs && pullUpEl.className.match('loading')) {
        pullUpEl.className = '';
        if (page >= pages) {
          pullUpEl.querySelector(upText).innerHTML = '数据已加载完...';
        } else {
          pullUpEl.querySelector(upText).innerHTML = '上拉加载更多...';
        }
      }
    },
    onScrollMove: function () {
      if (lock) {
        return;
      }
      if (this.y > (5 + pullDownOffset) && !pullDownEl.className.match('flip')) {
        pullDownEl.className = 'flip';
        pullDownEl.querySelector(downText).innerHTML = '松手开始更新...';
        this.minScrollY = 0;
        this.loadingY = pullDownOffset;
      } else if (this.y < (5 + pullDownOffset) && pullDownEl.className.match('flip')) {
        pullDownEl.className = '';
        pullDownEl.querySelector(downText).innerHTML = '下拉刷新...';
        this.minScrollY = 0;
      } else if (!onlyRefs) {
        if (this.y < (this.maxScrollY - 5) && !pullUpEl.className.match('flip')) {
          if (page >= pages) {
            scroll.refresh();
            return;
          }
          pullUpEl.className = 'flip';
          pullUpEl.querySelector(upText).innerHTML = '松手开始更新...';
          console.log(this.maxScrollY + "," + this.maxScrollY);
          this.maxScrollY = this.maxScrollY;
        } else if (this.y > (this.maxScrollY + 5) && pullUpEl.className.match('flip')) {
          if (page >= pages) {
            scroll.refresh();
            return;
          }
          pullUpEl.className = '';
          pullUpEl.querySelector(upText).innerHTML = '上拉加载更多...';
          this.maxScrollY = pullUpOffset;
        }
      }
    },
    onScrollEnd: function () {
      this.loadingY = 0;
      if (pullDownEl.className.match('flip')) {
        this.maxScrollY = pullDownOffset;
        pullDownEl.className = 'loading';
        pullDownEl.querySelector(downText).innerHTML = '加载中...';
        down();
      } else if (!onlyRefs && pullUpEl.className.match('flip')) {
        pullUpEl.className = 'loading';
        pullUpEl.querySelector(upText).innerHTML = '加载中...';
        up();
      } else {
        this.refresh();
      }
    }
  });

  setTimeout(function () {
    document.getElementById(config.wrapper).style.left = '0';
  }, 100);

  if (config.backFn) {
    config.backFn();
  }
}

export let loading = {
  ready(config){
    setTimeout(function () {
      loaded(config);
      //初始化绑定iScroll控件
      document.addEventListener('DOMContentLoaded', loaded, false);
    }, 100);
  },
  destroy(){
    scroll.destroy();
    scroll = null;
  },
  refs(_page, _pages){
    if (_page) {
      page = _page;
    }
    if (_pages) {
      pages = _pages;
      if (page >= pages) {
        pullUpEl.querySelector(upText).innerHTML = '数据已加载完...';
      } else {
        pullUpEl.querySelector(upText).innerHTML = '上拉加载更多...';
      }
    }
    scroll.refresh();
  },
  top(){
    /*pullDownEl.querySelector(downText).innerHTML = '加载中...';*/
    /*scroll.scrollTo(0, 0, 500, false);*/
    /*setTimeout(function () {*/
    scroll.scrollTo(0, 0, 0, false);
    down();
    /*}, 500);*/
  },
  lock(){
    lock = true;
  },
  unlock(){
    lock = false;
  },
  getName(){
    return name;
  },
  hasScroll(){
    return scroll != null;
  },
  upError(){
    pullUpEl.querySelector(upText).innerHTML = '加载失败...';
    setTimeout(function () {
      scroll.refresh();
    }, 500);
  },
  downError(){
    pullDownEl.querySelector(downText).innerHTML = '加载失败...';
    setTimeout(function () {
      scroll.refresh();
    }, 500);
  }
};




