<template>
    <div>
        <v-header>
            <div slot="left"></div>
            <!--       <div slot="right" class="h-right" v-if="isLogin"><a @clik="addView" v-link="{name:'publish-add'}" class="mg-r10">新建</a></div> -->
            <div slot="right" class="h-right" v-if="isLogin">
                <a @click="addView" class="mg-r10">新建</a>
            </div>
        </v-header>

        <div :class="['search-box','z-index-1',{'b-alone':standalone}]">
            <div class="warp">
                <ul class="search-ul dis-flex">
                    <li @click="searchDown(0)" :class="[status == 0 ? 'active':'']"><p>待发布</p>
                    </li>
                    <li @click="searchDown(1)" :class="[status == 1 ? 'active':'']"><p>发布中</p></li>
                    <li @click="searchDown(2)" :class="[status == 2 ? 'active':'']"><p>已失效</p></li>
                </ul>
                <div class="tran-forword search-bar" :style="barStyle"></div>
            </div>
        </div>

        <v-content :type="isLogin ? 'list' : ''">
            <v-publish-list class="publish-list" :status="status" v-if="isLogin">
            </v-publish-list>
            <div class="none_bg need-login" v-else>
                <span class="img bg01"></span>
                <p class="remark">亲，您还没有<a class="color-link"
                                           v-link="{name:'login',query:{from:'/publish?status='+status}}">登录</a>哟~</p>
                <!-- <input class="tip" value="登录"  type="button"/> -->
            </div>
        </v-content>

        <v-footer>
        </v-footer>
    </div>
</template>
<script>
    import VHeader from '../../components/Header'
    import VContent from '../../components/Content'
    import VFooter from '../../components/Footer'
    import VPublishList from '../../components/PublishList'
    import {DJUtils} from '../../assets/js/common'
    import {lt, url} from '../../const'
    import {toast} from '../../assets/js/toast'
    import $ from 'jquery'

    export default{
        route: {
            data({from, to}){
                if (this.isReady) {
                    this.$broadcast('reload-footer');
                    return {
                        state: !this.state,
                        isCurr: from.name == 'publish'
                    }
                }
            }
        },
        ready(){
            this.$broadcast('login-check');
            this.$on('ready', this.init);
            this.$emit('ready');
            this.isReady = true;
            this.standalone = DJUtils.isStandalone();

        }
        ,
        data(){
            return {
                isReady: false,
                state: false,
                isCurr: true,//跳转后是否在当前页

                status: 0,
                barStyle: {
                    left: '2%',
                    right: '68%'
                },
                isLogin: false,
                standalone: false
            }
        },
        methods: {
            init(){
                $('.none_bg').css('height', $(window).height() * 0.8 + "px");
                let vm = this;
                let query = vm.$route.query;
                if (query.status && !isNaN(query.status)) {
                    this.status = parseInt(query.status);
                    let left = 33 * this.status + 2;
                    let right = 100 - left - 30;
                    this.barStyle = {left: left + '%', right: right + '%'};
                }
            },
            searchDown(status){
                if (this.status == status) {
                    return;
                }
                this.status = status;
                let left = 33 * this.status + 2;
                let right = 100 - left - 30;
                this.barStyle = {left: left + '%', right: right + '%'};
                window.location.href = "#/publish?status=" + status;
                this.$broadcast('refs');
            },
            addView(){
                let vm = this;
                this.$http.get(url.publishVaild).then(
                    function ({data:{success, data}}) {
                        if (success) {
                            if (!data.isComplete) {
                                setTimeout(function () {
                                    toast.conform('请先完善个人信息，再发布', null, true, function () {
                                        window.location.href = '#/user/info?from=' + vm.$route.path;
                                    }, {do: '再逛逛', cancel: '去完善'});
                                }, 100);
                            } else if (data.isMaxPulish) {
                                toast.alert('您的服务存在数已达最大值！');
                            } else if (!data.isAuth) {
                                setTimeout(function () {
                                    toast.conform('请先实名认证，再发布', null, true, function () {
                                        window.location.href = '#/identify/real/step1?from=' + vm.$route.path;
                                    }, {do: '再逛逛', cancel: '去认证'});
                                }, 100);
                            } else {
                                window.location.href = '#/publish/add';
                            }
                        }
                    });
            }
        },
        events: {
            'is-login': function (isLogin) {
//        if(this.isLogin && !this.isCurr && isLogin == this.isLogin){
                if (this.isLogin && isLogin == this.isLogin && !this.isCurr) {
                    let vm = this;
                    setTimeout(function () {
                        vm.$broadcast('rescroll');
                    }, 100);
                    this.$broadcast('refs');
                }
                this.isLogin = isLogin;
            }
        },
        watch: {
            'state': function () {
//        if(!this.isCurr){
//          this.$broadcast('login-check');
//        }
                this.$broadcast('login-check');
                this.$emit('ready');
            }
        },
        components: {VHeader, VContent, VFooter, VPublishList}
    }
</script>
<style scoped>
    /*search-box*/
    .search-box {
        height: .45rem;
        position: absolute;
        top: .44rem;
        left: 0;
        right: 0;
        font-size: .16rem;
        line-height: .45rem;
    }

    .search-box.b-alone {
        top: .84rem;
    }

    .search-box .warp {
        height: 100%;
        background-color: #fff;
    }

    .search-ul {
        height: 100%;
        color: #999;
    }

    .search-ul li {
        text-align: center;
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
        position: relative;
        height: 100%;
        margin: 0 .2rem;
    }

    .search-ul li.active {
        color: #00cda9;
    }

    .search-bar {
        position: absolute;
        bottom: 0;
        left: 2%;
        right: 68%;
        height: .03rem;
        background-color: #00cda9;
    }

    .publish-list {
        z-index: 1;
        top: .89rem;
        left: 0;
        right: 0;
        bottom: .52rem;
        width: 100%;
    }

    .c-alone .publish-list {
        top: 1.09rem;
    }

    .img.bg01 {
        background-image: url(../../assets/img/bg/null_icon01.png);
    }
</style>




