<template>
  <div id="wrapper">
    <div id="scroller">
      <div id="pullDown">
        <span class="pullDownLabel" v-show="firstLoad">下拉刷新…</span>
        <span class="pullDownIcon"></span>
      </div>
      <ul class="service-box" v-if="items.length > 0">
        <li v-for="item in items" class="service">
          <ul :class="['tran-all-3','box',item.status == 2 ? 'invalid':'']">
            <li class="service-info" v-link="{name:'publish-show',params:{id:item.id}}">
              <p :class="['name ellipsis',item.status == 2 ? 'color-9':'']" style="max-width:75%;margin-top:0;">
                {{item.name}}</p>
              <p class="work-time">{{item.workTime}}</p>

              <p class="work-line" v-if="item.timeless">长期有效</p>
              <p class="work-line" v-else>{{item.startTime}}起共{{item.workDays}}天</p>

              <span :class="['status','color-'+status]">{{item.status | publish-status}}</span>
              <span class="price">¥{{item.price}}</span>
            </li>
            <li class="options">
              <span class="create-time">
                创建时间:{{item.createTime}}
              </span>
              <div class="btns">
                <input type="button" value="取消发布" class="cancel button-style" v-if="item.status==1"
                       @click="cancel(item.id,$index)"/>
                <input type="button" value="删除" class="destroy button-style" v-if="item.status==0"
                       @click="destroy(item.id,$index)" style="margin-right:.05rem;"/>
                <input type="button" value="发布" class="publish button-style" v-if="item.status==0"
                       @click="publish(item.id,$index)"/>
              </div>
            </li>
          </ul>
        </li>
      </ul>
      <div class="none_bg" v-if="items.length==0">
        <span class="img bg"></span>
        <p class="remark">
          暂无{{status | publish-status}}的任务
        </p>
      </div>
      <div id="pullUp">
        <span class="pullUpLabel" v-show="firstLoad && items.length > 0">上拉加载更多…</span>
        <span class="pullUpIcon"></span>
      </div>
    </div>
  </div>
</template>

<script>
  import {url} from '../const'
  import $ from 'jquery'
  import {loading} from '../assets/js/iscroll'
  import {toast} from '../assets/js/toast'

  export default{
    props: {
      status: {type: Number, default: 0, twoWay: true}
    },
    ready(){
      let vm = this;
      let query = vm.$route.query;
      if (query.status && !isNaN(query.status)) {
        this.status = parseInt(query.status);
      }
      this.addEvent();
      this.$http.get(url.publishList, this.linkData()).then(
              function (response) {
                this.firstLoad = true;
                let data = response.data.data;
                /*console.log(JSON.stringify(data));*/
                if (data) {
                  this.items = data;
                } else {
                  this.items = [];
                }
                this.pages = response.data.totalPage;
                this.resetScroll();
                loading.refs(this.page, this.pages);
              }, function (response) {
                toast.alert('网络异常');
              }
      )
      this.$on('rescroll', this.initScroll);
    },
    data(){
      return {
        items: [],
        page: 1,
        row: 10,
        pages: 1,
        t: new Date().getTime(),
        firstLoad: false
      }
    },
    methods: {
      down(){
        this.page = 1;
        this.t = new Date().getTime();
        this.$http.get(url.publishList, this.linkData()).then(
                function (response) {
                  let data = response.data.data;
                  /*console.log(JSON.stringify(data));*/
                  if (data) {
                    this.items = data;
                  } else {
                    this.items = [];
                  }
                  this.pages = response.data.totalPage;
                  this.resetScroll();
                  loading.refs(this.page, this.pages);
                }, function (response) {
                  loading.downError();
                }
        )
      },
      up(){
        this.page++;
        this.$http.get(url.publishList, this.linkData()).then(
                function (response) {
                  let data = response.data.data;
                  /* console.log(JSON.stringify(data));*/
                  if (data) {
                    for (let i in data) {
                      this.items.push(data[i]);
                    }
                  }
                  this.resetScroll();
                  loading.refs(this.page);
                }, function (response) {
                  loading.upError();
                }
        )
      },
      addEvent(){
        let vm = this;
        toast.loading();
        vm.initScroll();
        vm.$on('refs', function () {
          toast.loading();
          loading.top();
        });
      },
      initScroll(){
        let vm = this;
        let hasIn = false;
        if (loading.hasScroll()) {
          if (loading.getName() == 'publish-list') {
            hasIn = true;
          } else {
            loading.destroy();
          }
        }
        if (!hasIn) {
          vm.resetScroll();
          loading.ready({
            wrapper: 'wrapper',
            down: 'pullDown',
            downText: '.pullDownLabel',
            downFn: vm.down,
            up: 'pullUp',
            upText: '.pullUpLabel',
            upFn: vm.up,
            pages: this.pages,
            name: 'publish-list'
          });
        }
      },
      cancel(id, index){
        let vm = this;
        toast.conform('确定取消发布?', function () {
          vm.$http.get(url.changeStatus, {id: id, status: 0}).then(
                  function ({data:{data, success, resultMsg}}) {
                    if (success) {
                      vm.items.splice(index, 1);
                      toast.alert('已取消');
                    } else {
                      toast.alert(resultMsg);
                    }
                  }
          )
        });
      },
      destroy(id, index){
        let vm = this;
        toast.conform('确定删除?', function () {
          vm.$http.get(url.changeStatus, {id: id, status: 3}).then(
                  function ({data}) {
                    if (data && data.success) {
                      vm.items.splice(index, 1);
                      toast.alert('已删除');
                    } else {
                      toast.alert(data.resultMsg);
                    }
                  }
          )
        });

      },
      publish(id, index){
        this.$http.get(url.changeStatus, {id: id, status: 1}).then(
                function ({data}) {
                  if (data && data.success) {
                    this.items.splice(index, 1);
                    toast.alert('发布成功');
                  } else {
                    toast.alert(data.resultMsg);
                  }
                }
        )
      },
      linkData(){
        return {
          currentPage: this.page,
          pageSize: this.row,
          t: this.t,
          status: this.status
        }
      },
      resetScroll(){
        console.log(`${this.pages}->${this.page}`);
        let len = this.items.length * 1.6;
        let wLen = $(window).height() - 89 - 45;
        if ((len + 0.5) < wLen / 100) {
          $("#scroller").css({height: wLen + "px"});
        } else {
          $("#scroller").css({height: parseFloat(len + 0.1 + 0.4) + "rem"});
        }
      }
    },
    destroyed(){
      loading.destroy();
    }
  }
</script>
<style scoped>
  #wrapper {
    position: absolute;
  }

  .service-box {
    position: relative;
    width: 100%;
  }

  .service-box .service {
    background-color: #fff;
    height: 1.5rem;
    margin-top: .1rem;
    border-top: .01rem solid #efefef;
    border-bottom: .01rem solid #efefef;
    position: relative;
  }

  .service-box .service:last-child {
    margin-bottom: .1rem;
  }

  .service-box .box {
    position: relative;
    height: 100%;
  }

  /*.service-box .box:hover {*/
  /*background-color: #f1f1f1;*/
  /*}*/

  .box .service-info {
    color: #999;
    height: 1rem;
    text-align: left;
    position: relative;
    width: 100%;
    padding: .1rem;
  }

  .service-info p {
    margin-top: .08rem;
  }

  .service-info .name {
    font-size: .16rem;
    color: #333;
  }

  .name .area {
    font-size: .12rem;
    color: #999;
  }

  .service-info .work-time {
    font-size: .14rem;
    color: #666;
  }

  .service-info .status {
    font-size: .16rem;
    position: absolute;
    right: .1rem;
    top: .1rem;
  }

  .service-info .status.color-0{
    color:#1eb9ff;
  }

  .service-info .status.color-1{
    color:#78dc23;
  }

  .service-info .status.color-2{
    color:#999;
  }

  .service-info .price {
    position: absolute;
    right: .1rem;
    bottom: .1rem;
    color: #333;
  }

  .invalid .status, .invalid .price, .invalid .name {
    color: #999;
  }

  .box .options {
    border-top: .01rem solid #e6e6e6;
    line-height: .5rem;
  }

  .options .create-time {
    font-size: .12rem;
    color: #999;
    margin-left: .1rem;
  }

  .options .btns {
    float: right;
  }

  .options input {
    margin-right: .1rem;
  }

  .btns .cancel {
    width: .75rem;
    height: .3rem;
    border: .01rem solid #e6e6e6;
    background-color: #fff;
  }

  .btns .destroy, .btns .publish {
    width: .55rem;
    height: .3rem;
    border: .01rem solid #e6e6e6;
    background-color: #fff;
  }

  .btns .publish {
    border: .01rem solid #00cda9;
    color: #00cda9;
  }

  .img.bg {
    background-image: url(../assets/img/bg/null_icon01.png);
  }
</style>



