<template>
    <div class="footer z-index-1">
        <div class="warp">
            <ul class="menu">
                <li class="{{menu.clazz}}" v-for="menu in menus">
                    <a href="{{menu.link}}">
            <span class="img">
              <span class="notice" v-if="menu.id==3 && newOrder"></span>
              <span class="notice" v-if="menu.id==4 && unread"></span>
            </span>
                        <span>{{menu.name}}</span>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
    import {st, url, lt} from '../const'

    export default{
        ready(){
            this.$on('reload-footer', this.init)
            this.$emit('reload-footer');
        },
        data(){
            return {
                menus: [],
                newOrder: false,
                unread: false
            }
        },
        methods: {
            init(){
                let index = {id: 1, clazz: 'index', link: '#/home', name: '首页'};
                let favOrPub = {id: 2, clazz: 'favorite', link: '#/favorite', name: '候选'};
                let order = {id: 3, clazz: 'order', link: '#/order', name: '协议'};
                let user = {id: 4, clazz: 'user', link: '#/user', name: '我的'};

                let activeOrder = false;
                this.newOrder = false;
                const menu = st.getJson(st.keys.menu);
                if (menu) {
                    if (!menu.isHome) {
                        index.link = '#/home/server';
                        favOrPub.clazz = 'publish';
                        favOrPub.name = '发布';
                        favOrPub.link = '#/publish';
                    }
                    if (menu.name == 'home' || menu.name == 'home-server') {
                        index.clazz = 'index active';
                        index.link = "javascript:void(0)";
                    } else if (menu.name == 'favorite') {
                        favOrPub.clazz = 'favorite active';
                        favOrPub.link = "javascript:void(0)";
                    } else if (menu.name == 'publish') {
                        favOrPub.clazz = 'publish active';
                        favOrPub.link = "javascript:void(0)";
                    } else if (menu.name == 'order') {
                        console.log('menu order come in');
                        activeOrder = true;
                        order.clazz = 'order active';
                        order.link = "javascript:void(0)";
                    } else {
                        user.clazz = 'user active';
                        user.link = "javascript:void(0)";
                    }
                } else {
                    let name = this.$route.name;
                    if (name == 'favorite') {
                        favOrPub.clazz = 'favorite active';
                        favOrPub.link = "javascript:void(0)";
                    } else if (name == 'user') {
                        user.clazz = 'user active';
                        user.link = "javascript:void(0)";
                    } else if (name == 'order') {
                        activeOrder = true;
                        order.clazz = 'order active';
                        order.link = "javascript:void(0)";
                    } else {
                        index.clazz = 'index active';
                        index.link = "javascript:void(0)";
                    }
                }

                this.menus = [];
                this.menus.push(index);
                this.menus.push(favOrPub);
                this.menus.push(order);
                this.menus.push(user);

                this.newOrderNotify();
                this.noticeUnread();
            },
            newOrderNotify(){
                let vm = this;
                if (lt.has("user")) {
                    vm.$http.get(url.getNewAgreement).then(
                        function ({data}) {
                            if (data.success) {
                                // 新消息标记
                                if ((parseInt(data.data.buyNum) > 0 || parseInt(data.data.sellNum) > 0)) {
                                    vm.newOrder = true;
                                } else {
                                    vm.newOrder = false;
                                }
                                st.set(st.keys.newOrderInfo, data.data);
                                vm.$dispatch('newNotice');
                            }
                        }
                    )
                }
            },
            noticeUnread(){
                if (lt.has("user")) {
                    this.$http.get(url.noticeUnread).then(
                        function ({data:{success, data}}) {
                            if (success) {
                                this.unread = data > 0;
                            }
                        }
                    )
                }
            },
        },
        events: {
            'newOrder': function () {
                let newSend = st.getJson(st.keys.newOrderInfo).buyNum;
                let newAccept = st.getJson(st.keys.newOrderInfo).sellNum;
                if (newSend <= 0 && newAccept <= 0) {
                    this.newOrder = false;
                } else {
                    this.newOrder = true;
                }

            }
        }
    }
</script>
<style scoped>
    .footer {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
        height: .52rem;
        border-top: 0.01rem solid #c9c9c9;
    }

    .footer .warp {
        background-color: #f9f9f9;
    }

    .footer ul {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
        -webkit-align-items: center;
        align-items: center;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
    }

    .footer li {
        text-align: center;
        padding: .03rem 0 .07rem 0;
        -webkit-box-flex: 1;
        -ms-flex: 1;
        flex: 1;
    }

    .footer a {
        display: block;
        color: #c0c0c0;
        font-size: 0.1rem;
        line-height: 1;
        vertical-align: middle;
    }

    .footer span {
        display: block;
    }

    .menu .img {
        width: .24rem;
        height: .24rem;
        background-size: .24rem;
        margin: .05rem auto;
    }

    .menu .active span {
        color: #00cda9;
    }

    .menu .index .img {
        background-image: url(../assets/img/menu/home.png);
    }

    .menu .index.active .img {
        background-image: url(../assets/img/menu/home-a.png);
    }

    .menu .favorite .img {
        background-image: url(../assets/img/menu/star.png);
    }

    .menu .favorite.active .img {
        background-image: url(../assets/img/menu/star-a.png);
    }

    .menu .publish .img {
        background-image: url(../assets/img/menu/publish.png);
    }

    .menu .publish.active .img {
        background-image: url(../assets/img/menu/publish-a.png);
    }

    .menu .order .img {
        background-image: url(../assets/img/menu/agreement.png);
    }

    .menu .order.active .img {
        background-image: url(../assets/img/menu/argument-a.png);
    }

    .menu .user .img {
        background-image: url(../assets/img/menu/user.png);
    }

    .menu .user.active .img {
        background-image: url(../assets/img/menu/user-a.png);
    }

    .order .notice, .user .notice {
        background: #ff3737;
        border-radius: 50%;
        height: 7px;
        width: 7px;
        margin-left: 20px;
    }
</style>



